.switch{
    position: relative;
    display: inline-block;
    width: 80px;
    height: 50px;
}
.switch:hover{
    transform: scale(1.1);
    transition: transform 0.3s ease;
}
.switch input { 
    opacity: 0;
    width: 0;
    height: 0;
}
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 34px;
    border: solid 3px;
}
.slider:before {
    position: absolute;
    content: "";
    height: 34px;
    width: 36px;
    left: 8px;
    bottom: 7px;
    background-color: white;
    transition: .4s;
    border-radius: 50%;
}
input:checked + .slider {
    background-color: #333;
}
input:focus + .slider {
    box-shadow: 0 0 1px #333;
}
input:checked + .slider:before {
    -webkit-transform: translateX(28px);
    -ms-transform: translateX(28px);
    transform: translateX(28px);
}